@font-face {
  font-family: Questrial;
  src: url(/src/styles/font/Questrial-Regular.ttf) format("ttf");
  font-weight: normal;
  font-style: normal;
}
h1,
h2,
h3 {
  font-family: Questrial, sans-serif;
  font-weight: normal;
}

p {
  font-family: Questrial, sans-serif;
  font-size: 20px;
  line-height: 1.5;
}

a:hover {
  cursor: pointer;
  color: #0c84f4;
}

@media (max-width: 505px) {
  h1 {
    font-size: 20px;
  }
  h2 {
    font-size: 15px;
  }
  p {
    font-size: 12px;
    line-height: 1.2;
  }
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
